text, p {
  font-family: $fontStack;
  font-size: $fontSizeBig;
}

a {
  color: $accentColorDark;
  font-weight: 500;
}

a:hover {
  text-decoration: none;
}

h1 {
  font-family: $fontStackSerif;
  font-size: $fontSizeH1;
  line-height: $fontSizeBig;
}

h2 {
  font-family: $fontStackSerif;
  font-size: $fontSizeH2;
  line-height: $fontSizeBig;
  font-weight: normal;
}

.smallTxt {
  font-size: $fontSizeSmall;
  letter-spacing: $fontSizeSmall*.02;
}

.textRight {
  text-align: right;
}

.bold {
  font-weight: bold;
}

.textSecondary {
  color: $altColor;
}
.textCentered {
  text-align: center;
}

//for "required" fields
.redText {
  color: red;
}
