//for experimental layouts and testing

.redBg {
  background-color: red;
}

.debugging {
  display: none;
  color: blue;
  position:absolute;
  top: 1vh;
  right: 20%;
  z-index: 5;
}

.handsfree-video{
  position:fixed;
  bottom: 1vh;
  right: 1vh;
  width: 427px;
  height: 240px;
}

.handsfree-debugger {
  position:relative;
}

.handsfree-canvas {
  display:none;
}

//from handsfree site
.handsfree-pointer {
  width: 16px;
  height: 16px;
  border-radius: 15px;
  background: $accentColorDark;
  border: 3px solid $accentColorDark;
  position: fixed;
  top: -8px;
  left: -8px;
  z-index: 999999;
  opacity: 1;
  transition: opacity .35s ease;
  display: none;
}

.handsfree-clicked > .handsfree-pointer {
  background: white;
  border: 3px solid $accentColorDark;
}
