/* foundational variables on which site styles will be built */

/*
DEPRECATION WARNIN: Using / for division outside of calc() is deprecated and will be removed in Dart Sass 2.0.0.
remote:
remote: Recommendation: math.div($padding, 4) or calc($padding / 4)
*/
$color: #000;
$altColor: #888;
$bgColor: #eee;
$altBgColor: #DEDEDE;
$accentColorLight: #f2f2ff;
$accentColorDark: #6761D1;
$uiColor: rgba(255, 255, 255, 0.95);

$fontStack: -apple-system, BlinkMacSystemFont, 'Arial',
  sans-serif;
$fontStackSerif: 'Georgia', serif;
$fontSize: 1.2em;
$fontSizeSmall: $fontSize*.8;
$fontSizeBig: $fontSize;
$fontSizeH1: $fontSize*1.5;
$fontSizeH2: $fontSize*1.15;

$padding: 5%;
$paddingHalf: calc($padding/2);
$vPadding: 1.5vh;
